<template>
  <div class="form form-find" v-loading="loading">
    <el-form ref="form" :model="form" :rules="rules" label-position="top" size="medium" @submit.prevent.native="onSubmit('form')" v-if="!found">
      <el-form-item label="" prop="name">
        <el-input v-model="form.name" placeholder=") "></el-input>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input type="text" v-model="form.phone" placeholder=") 010-0000-0000"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button native-type="submit" plain class="btn-block"> </el-button>
      </el-form-item>
    </el-form>
    <div class="found" v-if="found">
         .
      <strong class="value">{{ email }}</strong>
      <div class="link-box">
        <router-link :to="'/find-account/password'" class="link"> ?</router-link>
        <router-link :to="'/signin'" class="link"> </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import Validator from '@/mixins/validator/Validator'

export default {
  name: 'FindEmail',
  mixins: [
    Validator
  ],
  data () {
    return {
      loading: false,
      found: false,
      email: '',
      form: {
        name: '',
        phone: ''
      },
      rules: {
        name: [
          { required: true, message: ' .', trigger: 'blur' },
          { min: 2, max: 5, message: ' 2  5  .', trigger: 'blur' }
        ],
        phone: [
          { required: true, message: '  .', trigger: 'blur' },
          { validator: this.validatePhone, message: '   .', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    init () {
      this.found = false
      this.email = ''
    },
    onSubmit (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          //  
          this.loading = true
          //   
          const formData = {
            name: this.form.name,
            phone: this.form.phone
          }
          //  data
          const data = await this.$store.dispatch('findEmail', formData)
          //   
          if (data) {
            this.found = true
            this.email = data
          }
          //  
          this.loading = false
        } else {
          return false
        }
      })
    }
  },
  watch: {
    '$route': 'init'
  },
  created () {
    this.init()
  }
}
</script>



